@import url(https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c:400,500,700,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

body {
  font-size: 22px;
  font-family: "M PLUS Rounded 1c", sans-serif;
  color: #2E2D33; }

.App {
  margin: 0 0 1.5rem 0; }
  .App .title {
    margin-bottom: 3rem; }

.card {
  background-color: #FFFFFF;
  width: 100%;
  border-radius: 0.5rem;
  padding: 2rem;
  border: 2px solid #E5E5E5;
  box-shadow: 0px -6px inset #E5E5E5; }
  .card-title {
    margin: 0; }
  .card-subtitle {
    color: #666666; }
  .card-description {
    text-align: center;
    font-size: 3.5rem;
    margin: 1rem 0 2rem;
    line-height: 3.5rem; }
  .card-tags {
    display: inline-block; }

.cards-group {
  margin: 0;
  padding: 0; }
  .cards-group-item {
    margin-bottom: 1rem; }
  .cards-group .slick-slide > div {
    margin: 0 1rem; }
  .cards-group .slick-dots {
    bottom: 0.65rem; }
    .cards-group .slick-dots li {
      width: 50px;
      height: 2px;
      margin: 0 2px;
      padding: 0; }
      .cards-group .slick-dots li button {
        font-size: 0;
        line-height: 0;
        width: 100%;
        height: 100%;
        padding: 0; }
        .cards-group .slick-dots li button:before {
          width: 100%;
          height: 100%;
          opacity: .25;
          background-color: #2E2D33;
          font-family: none;
          line-height: 0;
          font-size: unset;
          border-radius: 2px; }

.header {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  background: #673AB7;
  color: #FFFFFF;
  width: 100%;
  padding: 1rem;
  top: 0;
  box-shadow: 0 -4px #46287d inset;
  font-weight: bold;
  text-transform: uppercase;
  font-size: smaller;
  margin-bottom: 1rem; }

.tag {
  font-size: 0.875rem;
  display: inline-block;
  background-color: #E5E5E5;
  color: #666666;
  padding: 0.2rem 0.6rem;
  border-radius: 4px;
  margin-right: 0.4rem;
  text-transform: uppercase;
  font-weight: 800; }

